import React from 'react'
import { useLocalStore, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
// import { BlogStore, BlogChildStore } from './model';
import { store } from '../../store';

const { BlogStore, BlogChildStore } = store;

export const Blog = observer((props: any) => {
    const [number, setNumber] = React.useState<number>(0);
    const $BlogChildStore = useLocalStore(() => BlogChildStore);
    return <>Blog: 
    数组: { JSON.stringify(BlogStore.blogList) }
    和: { BlogStore.sum }
    <input type="text" value={number} onChange={e => setNumber(+e.target.value)} />
    <button onClick={() => BlogStore.add(number)}>add</button>
    <button onClick={() => BlogStore.fetchValue()}>fetch</button>

    {$BlogChildStore.title}
    <input type="text" value={$BlogChildStore.title} onChange={(e) => { $BlogChildStore.title = e.target.value }}/>
    <Link to='/home'>to home</Link>
    </>
})