import React from 'react';
import { useLocalStore, Observer } from 'mobx-react';
import { store } from '../../store'

export const Shopping:React.FC = ((props: any) => {
    const $store = useLocalStore(() => store.ShoppingStore);

    return <Observer>{() => (<>
        购物: { $store.computedPrice }
        <button onClick={() => $store.setPrice()}>add</button>
        </>)}
    </Observer>;
})