import * as React from 'react';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { Redi } from 'react-router'
import { computedRouteArray } from './routes';

function Render(props: any) {
  const {
    routes,
    prefix,
  } = props;
  return (
    <>
    <Router>
      <Switch>
        {
          computedRouteArray(routes, prefix)
            .map((route: any, index: number) => <Route key={route.path + index} path={route.path} render={(props: any) => 
              <route.component {...props}/>}
            />)
        }
        <Redirect from='/*' to='/home'/>
      </Switch>
    </Router>
    </>
  );
}

export default Render;
