export const HomePage:React.FC = observer((props: any) => {
    const screenRef = useRef<HTMLElement|null>(null);
    const handleScrollMove = () => {
        const { top: Top, height } = screenRef.current.getBoundingClientRect();
        const top = Top < 0 ? -Top : 0;
        const screenHeight = window.screen.height;
        const start = 600;
        const end = height - screenHeight - 800;
        if (top < start) {
            const bf_co = Math.floor(top / start * 100) / 100
            
            document.documentElement.style.setProperty('--bf-co', `${bf_co}`);
        } 
        if (top >= start && top < end) {
            const co = Math.floor((top - start) / (end - start) * 100) / 100
            document.documentElement.style.setProperty('--co', `${co}`);
        } 
        if (top - 300 >= end) {
            const af_co = Math.floor((top - 300 - end) / 500 * 100) / 100;
            document.documentElement.style.setProperty('--af-co', `${af_co}`);
        }
    }
    const [search, setSearch] = useState<string>('');
    const [focus, setFocus] = useState<boolean>(false);
    
    useEffect(() => {
        // 此页面禁止自动滚动
        window.history.scrollRestoration = 'manual'
        window.addEventListener('scroll', handleScrollMove, false);
        handleScrollMove();
        return function () {
            window.history.scrollRestoration = 'auto'
            window.removeEventListener('scroll', handleScrollMove, false);
        }
    }, []);
    return <div className='homepage'>
        <Screen 
            ref={screenRef} 
            setSearch={setSearch} 
            search={search}
            setFocus={setFocus}
            focus={focus}
            />
            <header>
            <div className="logo_box">
                
            </div>
                <div className='btn_group'>
                    <label>
                        <input 
                            className={focus ? 'focus' : ''} 
                            type="text"
                            placeholder='...'
                            value={search} 
                            onChange={(e) => setSearch(e.target.value)} 
                            onFocus={() => setFocus(true)}
                            onBlur={() => setFocus(false)}
                        />
                        <button className='search_btn'>search</button>
                    </label>
                    <button className='login_btn'>login</button>
                </div>
            </header>
            <div className="logo_header">

            </div>
            <div style={{height: 100}}></div>
            <h2>
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Accusantium vitae soluta animi voluptatem, nostrum impedit fugit! Praesentium, minus iusto natus libero earum est voluptates necessitatibus enim ea, modi, maiores voluptas.
            </h2>

            <p>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Inventore placeat omnis odit facere quo corporis perferendis! Consequuntur, delectus deleniti voluptas officiis similique sint vero doloremque nostrum odio neque nemo aut dicta commodi officia nam quam non voluptate corporis nobis deserunt et sunt facere cum. A ad ipsa qui dolor, omnis sit quos tempore beatae repudiandae vel officiis corporis voluptates sapiente! Est adipisci aperiam dicta quibusdam, autem, perferendis a possimus quaerat sit laborum impedit ut. Excepturi illum aperiam sapiente iste odio fugiat harum? Accusantium sint, neque iure est perferendis nesciunt corrupti. Itaque iure eum doloribus minima, ut sequi sapiente eligendi quae.
            </p>
    </div>
})