import { makeObservable, action, observable, computed, flow } from 'mobx';

const DEFAULT_VALUE = {
    title: 'blog',
    list: [],
    count: 0,
};
const createStore = (target?: any) => {
    return makeObservable({
        ...DEFAULT_VALUE,
        get blogList() {
            return this.list;
        },
        add(item: number) {
            this.list.push(item);
        },
        countAdd() {
            console.log('this.count', this.count);
            this.count++;
        },
        get sum() {
            return this.list.reduce((acc, tar) => { 
                acc+= tar
                return acc
            }, 0);
        },
        * fetchValue () {
            const value = new Promise((resolve, reject) => setTimeout(() => resolve({ code: 200, message: 'SUCCESS', data: [1, 2, 3] }), 3000));
            const { code, data } = yield value;
            if (code === 200 ) {
                this.list = data;
            }
        }
    }, 
    {
        title: observable,
        list: observable,
        count: observable,
        add: action,
        countAdd: action,
        sum: computed,
        fetchValue: flow,
    });
};

export const BlogChildStore = ({
    title: 'BlogChildStore',
});

export const BlogStore = createStore();