import { makeObservable, observable, action, computed } from 'mobx'

class ShoppingModel {
    title = 'shop';
    shoppinglist = [1, 2, 3]; // 每样商品购买数量
    price = 3; // 单价
    get computedPrice() {
        return this.shoppinglist.reduce((acc: number, tar: number) => {
            acc += tar * this.price;
            return acc;
        }, 0);
    }
    constructor() {
        makeObservable(this, {
            title: observable,
            computedPrice: computed,
            price: observable,
            setPrice: action,
        });
    }

    setPrice () {
        console.log(this)
        this.price++;
    }
    getComputePrice () {
        return this.shoppinglist.reduce((acc: number, tar: number) => {
            acc += tar * this.price;
            return acc;
        }, 0);
    }
    
}

export const ShoppingStore = new ShoppingModel();