import { ReactNode } from 'react'
import { HomePage, Shopping, List, Blog } from './pages'

interface IRoute {
    path: string;
    component: ReactNode;
    routes?: Array<IRoute>;
}

export const routes: Array<IRoute>  = [
    {
        path: "/home",
        component: HomePage,
    },
    {
        path: "/Shopping",
        component: Shopping,
    },
    {
        path: "/blog",
        component: Blog,
        routes: [
            {
                path: "/list/:name",
                component: List,
            },
        ],
    },
]

export const computedRouteArray = function fn(list: Array<IRoute>, prefix: string) {
    if (prefix === void 0 || prefix === null) prefix = '';
    return list.reduce((acc, { path, routes = [], component }) => {
      const hasChild = !!routes.length;
      return [
        ...hasChild ? fn(routes, prefix + path) : [],
        { 
          path: prefix + path, 
          component,
        },
        ...acc,
      ]
    }, []);
}