import React from 'react';
import { makeAutoObservable, observable } from 'mobx'
import { observer } from 'mobx-react'

class ListModel {
    count = 0;
    name = 'alex';
    constructor () {
        makeAutoObservable(this);
    }
    add () {
        this.count++;
    }

    changeName () {
        this.name = this.name + 'a';
    }
}
const ListStore = new ListModel()

const ListItem = observer((props: any) => {
    const { list } = props;
    return <>
    {list?.name}
    <button onClick={()=> list.add()}>add</button>
    </>
})

const ListItem2 = observer((props: any) => {
    const { list } = props;
    return <>
    {list?.count}
    <button onClick={()=> list.changeName()}>add</button>
    </>
})

export const List = () => {
    return <>
            this is list
        </>
}